 <template>
  <div>
    <mu-appbar style="width: 100%;" class="header backgroundNaviBar">
      <a @click="()=>{
          $router.go(-1)
          }">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span style="color:white">提交解决</span>
    </mu-appbar>
    <div class="body">
      <div>
        <div class="flexBox flexHeight">
          <div style="flex:2">
            <span>解决时间</span>
          </div>
          <div style="flex:5" class="flexBox" @click="()=>{show=true}">
            <div>
              <input type="text" class="input" disabled placeholder="请选择解决时间" v-model="SelectTime" />
            </div>
            <div class="imageSize">
              <img src="static/img/toBottom.png" alt />
            </div>
          </div>
        </div>
        <div class="flexBox flexHeight">
          <div style="flex:2">
            <span>解决人</span>
          </div>
          <div style="flex:5">
            <div>
              <input type="text" class="input" placeholder="请输入解决人员" v-model="person" />
            </div>
          </div>
        </div>
        <div class="flexBox flexHeight">
          <div style="flex:2">
            <span>解决方案</span>
          </div>
          <!-- <div style="flex:5">
            <div>
              <input type="text" class="input" placeholder="请输入解决方案" v-model="solution" />
            </div>
          </div>-->
        </div>
        <div class="flexBox" style="background:white;">
          <textarea name id="textarea" class="textarea" placeholder="请输入解决方案" v-model="solution"></textarea>
        </div>
        <div style="background:white;padding:10px;">
          <div style="height:40px;">
            <span>图片</span>
          </div>
          <div>
            <van-uploader v-model="fileList" multiple :after-read="afterRead" />
          </div>
        </div>

        <div class="flexBoxBottom">
          <div class="close" @click="()=>{
            $router.go(-1);
            }">
            <span>取消</span>
          </div>
          <div class="submit backgroundNaviBar" @click="submitAlert()">
            <span>提交</span>
          </div>
        </div>
      </div>
    </div>
    <van-popup v-model="show" position="bottom">
      <van-datetime-picker
        v-model="currentDate"
        type="datetime"
        :min-date="minDate"
        :max-date="maxDate"
        @confirm="confirm"
        @cancel="()=>{
            show=false
        }"
      />
    </van-popup>
    <van-overlay :show="showover" @click="showover = false" class="overlay">
      <van-loading type="spinner" color="#1989fa" />
    </van-overlay>
  </div>
</template>

<script>
import Vue from "vue";
import { Uploader } from "vant";
import { Toast } from "vant";
Vue.use(Uploader);
import { Popup } from "vant";
import { DatetimePicker } from "vant";
import { Overlay } from "vant";
import { Loading } from "vant";
import { Dialog } from "vant";
Vue.use(Loading);
Vue.use(Overlay);
Vue.use(DatetimePicker);
Vue.use(Popup);
import AppBace from "../../../static/ajax";
export default {
  data() {
    return {
      fileList: [],
      formdate: new FormData(),
      show: false,
      minDate: new Date(2020, 0, 1),
      maxDate: new Date(2025, 10, 1),
      currentDate: new Date(),
      SelectTime: "",
      person: "",
      solution: "",
      token: "",
      showover: false
    };
  },
  methods: {
    confirm(val) {
      console.log(val);
      this.show = false;
      this.SelectTime = AppBace.filterTime(val);
    },
    afterRead(file) {
      this.formdate.append("files", file.file);
    },
    submitAlert() {
      var _this = this;
      Dialog.confirm({
        // title: "提交表格",
        message: "确认提交？"
      })
        .then(() => {
          _this.showover = true;
          _this.submit();
        })
        .catch(() => {
          // on cancel
          Toast.fail("已取消");
        });
    },
    async submit() {
      var url = "/job/operate-record/solution";

      if (this.person == "" || this.solution == "" || this.SelectTime == "") {
        Toast.fail("请完善信息");
        this.showover = false;
      } else {
        this.formdate.append("gmtFinish", this.SelectTime);

        this.formdate.append("id", this.$store.state.controlContent.id);
        this.formdate.append("solvePerson", this.person);
        this.formdate.append("solution", this.solution);
        var res = await AppBace.request(url, "POST", this.formdate, this.token);
        console.log(res);
        if (res.data.message === "success") {
          Toast.success("提交成功");
          this.$router.go(-2);
        } else {
          Toast.fail("失败，请重试");
        }
        this.showover = false;
      }
    }
  },
  created() {
    this.token = sessionStorage.getItem("token");
  }
};
</script>
<style  scoped>
.flexBox {
  display: flex;
  justify-content: space-between;
}
.imageSize img {
  width: 16px;
  height: 16px;
}
.flexHeight {
  height: 50px;
  display: flex;
  align-items: center;
  background-color: white;
  padding: 0 10px;
  border-bottom: 1px solid rgb(235, 235, 235);
}
.input {
  background-color: white;
  border: none;
}
.imageSize {
  display: flex;
  align-items: center;
}
.flexBoxBottom {
  background-color: white;
  display: flex;
  justify-content: space-evenly;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 40px;
  align-items: center;
}
.close {
  border: 1px grey solid;
  color: grey;
  border-radius: 20px;
  padding: 2px 20px;
}
.submit {
  /* border: 1px blue solid; */
  /* background-color: blue; */
  color: white;
  padding: 2px 20px;
  border-radius: 20px;
}
#textarea {
  /* border-radius: 10px; */
  width: 100%;
  height: 100px;
  border: 1px solid rgb(235, 234, 234);
  padding: 10px;
}
.overlay {
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>



